* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#header {
  width: 100%;
  height: 80vh;
  margin-top: 60px;
}

#header h1 {
  margin: 0 0 10px 0;
  font-size: 40px;
  font-weight: 800;
  color: #333;
}

#header p {
  font-size: 32px;
}

#header .company {
  color: #00dfc0;
}

#header h2 {
  font-size: 24px;
  color: #484848;
}

#header .btn-started {
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 10px 30px;
  border-radius: 50px;
  transition: 0.5s;
  color: #00dfc0;
  border: 2px solid #00dfc0;
  text-decoration: none;
}

#header .btn-get-started:hover {
  background: #00dfc0;
  color: #fff;
}

.myimg {
  text-align: right;
  margin-top: 20px;
}

#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

.carousel-item img {
  object-fit: cover;
  position: center;
}

.mycard {
  margin: 30px 5px;
  padding-top: 50px;
}


